import {
  InfoCircleOutlined,
  UserOutlined,
  PlusCircleOutlined,
  DeleteOutlined,
} from '@ant-design/icons';
import { Button, Menu, Popover, Select } from 'antd';
import React, { useCallback, useEffect, useState } from 'react';
import { useHistory, useLocation } from 'react-router-dom';
import logo from '../assets/logo.svg';
import styled from 'styled-components';
import { useWallet, WALLET_PROVIDERS } from '../utils/wallet';
import {
  useConnectionEndpoints,
  useConnectionConfig,
  DEFAULT_ENDPOINTS,
} from '../utils/connection';
import LinkAddress from './LinkAddress';
import CustomConnectionDialog from './CustomConnectionDialog';

const Wrapper = styled.div`
  background-color: #0d1017;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding: 0px 30px;
  flex-wrap: wrap;
`;
const LogoWrapper = styled.div`
  display: flex;
  align-items: center;
  color: #2abdd2;
  font-weight: bold;
  cursor: pointer;
  img {
    height: 30px;
    margin-right: 8px;
  }
`;

export default function TopBar() {
  const [current, setCurrent] = useState('/');
  const { connected, wallet, providerUrl, setProvider } = useWallet();
  const { endpoint, setEndpoint } = useConnectionConfig();
  const [showConnectionDialog, setShowConnectionDialog] = useState(false);
  const [
    endpoints,
    customEndpoints,
    setCustomEndpoints,
  ] = useConnectionEndpoints();
  const location = useLocation();
  const history = useHistory();

  const onAddConnectionEndpoint = (connectionEndpoint) => {
    setCustomEndpoints([...customEndpoints, connectionEndpoint]);
    setEndpoint(connectionEndpoint.endpoint);
  };

  const onDeleteConnectionEndpoint = (endpoint) => {
    setCustomEndpoints(
      customEndpoints.filter((entry) => entry.endpoint !== endpoint),
    );
    setEndpoint(DEFAULT_ENDPOINTS[0].endpoint);
  };

  const publicKey = wallet?.publicKey?.toBase58();

  const handleClick = useCallback(
    (e) => {
      history.push(e.key);
    },
    [history],
  );

  useEffect(() => {
    if (location.pathname.includes('/orders')) {
      setCurrent('/orders');
    } else if (location.pathname.includes('/balances')) {
      setCurrent('/balances');
    }
  }, [location]);

  return (
    <Wrapper>
      <LogoWrapper>
        <img src={logo} alt="" />
        {'SERUM'}
      </LogoWrapper>
      <Menu
        mode="horizontal"
        onClick={handleClick}
        selectedKeys={[current]}
        style={{
          borderBottom: 'none',
          backgroundColor: 'transparent',
          display: 'flex',
          alignItems: 'flex-end',
          flex: 1,
        }}
      >
        <Menu.Item key="/">TRADE</Menu.Item>
      </Menu>
      <div>
        <Select
          onSelect={setEndpoint}
          value={endpoint}
          style={{ marginRight: 8, width: 150 }}
        >
          {endpoints.map(({ name, endpoint, custom }) => (
            <Select.Option value={endpoint} key={endpoint}>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'space-between',
                }}
              >
                <div>{name}</div>{' '}
                <div>
                  {custom ? (
                    <DeleteOutlined
                      style={{ color: '#2abdd2' }}
                      onClick={() => onDeleteConnectionEndpoint(endpoint)}
                    />
                  ) : null}
                </div>
              </div>
            </Select.Option>
          ))}
          <Select.Option value={null} key={'new'} disabled={true}>
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <PlusCircleOutlined
                style={{ color: '#2abdd2', marginTop: 4 }}
                onClick={() => setShowConnectionDialog(true)}
              />
            </div>
          </Select.Option>
        </Select>
      </div>
      <div>
        <Select onSelect={setProvider} value={providerUrl}>
          {WALLET_PROVIDERS.map(({ name, url }) => (
            <Select.Option value={url} key={url}>
              {name}
            </Select.Option>
          ))}
        </Select>
      </div>
      <div>
        <Button
          type="text"
          size="large"
          onClick={connected ? wallet.disconnect : wallet.connect}
          style={{ color: '#2abdd2' }}
        >
          <UserOutlined />
          {!connected ? 'Connect wallet' : 'Disconnect'}
        </Button>
        {connected && (
          <Popover
            content={<LinkAddress address={publicKey} />}
            placement="bottomRight"
            title="Wallet public key"
            trigger="click"
          >
            <InfoCircleOutlined style={{ color: '#2abdd2' }} />
          </Popover>
        )}
      </div>
      <CustomConnectionDialog
        visible={showConnectionDialog}
        onClose={() => setShowConnectionDialog(false)}
        onAddConnectionEndpoint={onAddConnectionEndpoint}
      />
    </Wrapper>
  );
}
