import React, { useState } from 'react';
import { Modal, Input, Row, Col } from 'antd';
import { notify } from '../utils/notifications';

export default function CustomConnectionDialog({
  visible,
  onAddConnectionEndpoint,
  onClose,
}) {
  const [endpoint, setEndpoint] = useState('');
  const [name, setName] = useState('');
  const onSubmit = () => {
    if (!name || !endpoint) {
      notify({ message: 'Please fill in all fields', type: 'error' });
      return;
    }
    if (!valid) {
      notify({ message: 'Please supply a valid URL', type: 'error' });
      return;
    }
    onAddConnectionEndpoint({ name, endpoint, custom: true });
    onClose();
  };
  const valid = isValidUrl(endpoint);
  const canSubmit = name && endpoint;

  return (
    <Modal
      title={'Add custom connection'}
      visible={visible}
      onOk={onSubmit}
      okText={'Add'}
      onCancel={onClose}
      okButtonProps={{ disabled: !canSubmit }}
    >
      <div>
        <Row style={{ marginBottom: 8 }}>
          <Col span={24}>
            <Input
              placeholder="Name"
              value={name}
              onChange={(e) => setName(e.target.value)}
            />
          </Col>
        </Row>
        <Row style={{ marginBottom: 8 }}>
          <Col span={24}>
            <Input
              placeholder="Endpoint"
              value={endpoint}
              onChange={(e) => setEndpoint(e.target.value)}
            />
          </Col>
        </Row>
      </div>
    </Modal>
  );
}

function isValidUrl(string) {
  try {
    new URL(string);
  } catch (_) {
    return false;
  }
  return true;
}
